import React, { useEffect, useState, useContext } from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import Typography from '@material-ui/core/Typography';
import Pagination from '@material-ui/lab/Pagination';
import { socket } from '../context/SocketContext';
import { ServerContext } from '../context/ServerContext';

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    body: {
        fontSize: 14,
    },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.action.hover,
        },
    },
}))(TableRow);

const useStyles = makeStyles((theme) => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: 200,
        },
    },
    // table: {
    //     minWidth: 700,
    // },
    errortext: {
        color: "#ff0000",
    },
}));

export default function HistoryTable() {
    const classes = useStyles();
    const { access } = useContext(ServerContext)[0];

    let [eventdata, setEventdata] = useState({
        rows: [],
        total: 0,
    });

    let [filter, setFilter] = useState({
        type: ['order', 'event'],
        event: '',
        page: '1',
    });

    let [history, setHistory] = useState({
        logs: [],
        total: 0,
    });

    const handleHistoryChange = (hist) => {
        let rows = []
        hist.logs.reverse().forEach((log) => {
            if (log.type !== 'order') log.type = 'event';
            rows.push({
                time: log.time,
                type: log.type,
                event: log.event,
            });
        });
        setEventdata({
            rows: rows,
            total: hist.total,
        })
    }

    useEffect(() => {
        handleHistoryChange(history);

        return () => {
        }
    }, [history]);

    const handleEvent = (log, summary) => {
        if (socket && access.allow) socket.emit("get-logs", filter, access.token);
    };

    useEffect(() => {
        socket.on("event-history", (data) => {
            data.logs.forEach((log) => {
                if (log.type !== 'order') log.type = 'event';
            });
            setHistory({
                logs: data.logs,
                total: data.total,
            });
        });
        socket.on("event", function (data) {
            if (data.log.type !== 'order') data.log.type = 'event';
            handleEvent(data.log, data.summary);
        });

        return () => {
            socket.off("event-history");
        }
    }, []);

    useEffect(() => {
        if (socket && access.allow) socket.emit("get-logs", filter, access.token);

        return () => {
        }
    }, [filter]);

    const handlePage = (event, newPage) => {
        setFilter({
            type: filter.type,
            event: filter.event,
            page: newPage,
        });
    };

    const handleTypeFilter = (event, newTypes) => {
        setFilter({
            type: newTypes,
            event: filter.event,
            page: filter.page,
        });
    };

    const handleEventFilter = (e) => {
        setFilter({
            type: filter.type,
            event: e.target.value,
            page: filter.page,
        });
    }

    return (
        <Paper pt={2}>
            <Grid container style={{ height: '60px' }} alignItems="center">
                <Box p={1}>
                    <Grid item xs={3}>
                        <ToggleButtonGroup value={filter.type} onChange={handleTypeFilter} aria-label="type selection">
                            <ToggleButton value="order" aria-label="order" style={{ height: '30px' }} >
                                <Typography variant="overline" noWrap>
                                    Orders
                                </Typography>
                            </ToggleButton>
                            <ToggleButton value="event" aria-label="event" style={{ height: '30px' }} >
                                <Typography variant="overline" noWrap>
                                    Alerts
                                </Typography>
                            </ToggleButton>
                        </ToggleButtonGroup>
                    </Grid>
                </Box>
                <Grid item xs={2}>
                    <form className={classes.root} noValidate autoComplete="off">
                        <TextField id="event-filter" label="Filter events" color="secondary" onChange={handleEventFilter} />
                    </form>
                </Grid>
                <Grid item xs={7}></Grid>
            </Grid>
            <TableContainer component={Paper}>
                <Table className={classes.table} size="small" aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell style={{ width: '170px' }}>Time</StyledTableCell>
                            <StyledTableCell>Event</StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {eventdata.rows.map((row, i) => (
                            <StyledTableRow key={i}>
                                <StyledTableCell component="th" scope="row">
                                    <Typography variant="caption">{row.time}</Typography>
                                </StyledTableCell>
                                <StyledTableCell>
                                    {(row.type === 'event') ? (
                                        <Typography variant="body2" className={classes.errortext}>{row.event}</Typography>
                                    ) : (
                                        <Typography variant="body2">{row.event}</Typography>
                                    )}
                                </StyledTableCell>
                            </StyledTableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <Box display="flex" justifyContent="flex-end" >
                <Pagination count={Math.ceil(eventdata.total / 20)} shape="rounded" onChange={handlePage} />
            </Box>
        </Paper>
    );
}
