import React, { useState, useContext } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import BackupIcon from '@material-ui/icons/Backup';
import { useTheme } from '@material-ui/core/styles';
import { ConfigurationContext } from '../context/ConfigurationContext';

const useTableHeadStyles = makeStyles((theme) => ({
    headcolor:
        theme.palette.type === 'light'
            ? {
                backgroundColor: '#aaaaaa',
            }
            : {
                backgroundColor: '#777777',
            },
}));

function EnhancedTableHead(props) {
    const { onSelectAllClick, numSelected, rowCount, headless } = props;
    const classes = useTableHeadStyles();

    const headCells = [
        { id: 'coin', align: 'left', width: '60px', disablePadding: true, label: 'Coin' },
        { id: 'amount', align: 'right', width: '100px', disablePadding: false, label: 'Amount' },
        { id: 'price', align: 'right', width: '110px', disablePadding: false, label: 'Price' },
        { id: 'value', align: 'right', width: '110px', disablePadding: false, label: 'Value' },
        { id: 'distribution', align: 'right', width: '80px', disablePadding: false, label: 'Portfolio %' },
        { id: 'target', align: 'right', width: '80px', disablePadding: false, label: 'Target %' },
        { id: 'trade', align: 'center', width: '100px', disablePadding: false, label: 'Trade' },
        { id: 'side', align: 'center', width: '100px', disablePadding: false, label: 'Order Side' },
    ];

    let checkbox;
    if (!headless) {
        checkbox = <Checkbox
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{ 'aria-label': 'select coins' }}
        />
    } else {
        checkbox = null;
    }
    return (
        <TableHead>
            <TableRow className={classes.headcolor}>
                <TableCell padding="checkbox">
                    {checkbox}
                </TableCell>
                {headCells.map((headCell) => (
                    <TableCell
                        key={headCell.id}
                        align={headCell.align}
                        padding={headCell.disablePadding ? 'none' : 'default'}
                        style={{ minWidth: headCell.width }}
                    >
                        <TableSortLabel>
                            {headCell.label}
                        </TableSortLabel>
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    );
}

EnhancedTableHead.propTypes = {
    classes: PropTypes.object.isRequired,
    numSelected: PropTypes.number.isRequired,
    onSelectAllClick: PropTypes.func.isRequired,
    rowCount: PropTypes.number.isRequired,
    headless: PropTypes.bool.isRequired,
};

const useToolbarStyles = makeStyles((theme) => ({
    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    title: {
        flex: '1 1 100%',
    },
}));

const EnhancedTableToolbar = (props) => {
    const classes = useToolbarStyles();
    const { selected, headless, portfolio, handleOrder, setSelected, basecoin } = props;
    const numSelected = selected.length;

    function onOrder() {
        setSelected([]);
        let selectedCoins = [];
        for (let i = 0; i < selected.length; i++) {
            let selectedCoin = portfolio.find(({ coin }) => coin === selected[i]);
            if (selectedCoin.coin !== basecoin) selectedCoins.push(selectedCoin);
        }

        handleOrder(selectedCoins);
    }

    return (
        <Toolbar
            className={clsx(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >
            {numSelected > 0 ? (
                <Typography className={classes.title} color="inherit" variant="subtitle1" component="div">
                    {numSelected} {(numSelected === 1) ? 'coin' : 'coins'} selected: {selected.join()}
                </Typography>
            ) : (
                <Typography className={classes.title} variant="subtitle1" id="tableTitle" component="div">
                    {(!headless) ? ('Select coins to manually submit order') : ('Running in automatic mode')}
                </Typography>
            )}

            {(numSelected > 0) && (
                <Button variant="contained" color="secondary" style={{ width: '220px' }} startIcon={<BackupIcon />} onClick={onOrder}>
                    Submit Order
                </Button>
            )}
        </Toolbar>
    );
};

EnhancedTableToolbar.propTypes = {
    selected: PropTypes.array.isRequired,
    headless: PropTypes.bool.isRequired,
    portfolio: PropTypes.array.isRequired,
    handleOrder: PropTypes.func.isRequired,
    setSelected: PropTypes.func.isRequired,
    basecoin: PropTypes.func.isRequired,
};

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    paper: {
        width: '100%',
        marginBottom: theme.spacing(2),
    },
    table: {
        minWidth: 750,
        "& .MuiTableCell-sizeSmall": {
            paddingTop: '9px',
            paddingBottom: '9px',
        }
    },
    tableRow: {
        height: 50,
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
    },
}));

function formatResult(num, dec) {
    // Complex logic needed to have fixed decimals, avoid scientific notation, and to deal with numbers that already have scientific notation
    let result;
    if (!("" + num).includes("e")) {
        result = +(Math.round(num + "e+" + dec) + "e-" + dec);
    } else {
        let arr = ("" + num).split("e");
        let sig = ""
        if (+arr[1] + dec > 0) {
            sig = "+";
        }
        result = +(Math.round(+arr[0] + "e" + sig + (+arr[1] + dec)) + "e-" + dec);
    }

    let len = result.toString().split('.')[0].length;

    return Number(result).toFixed(dec + 1 - len);
}

export default function BalancesTable(props) {
    const classes = useStyles();
    const [selected, setSelected] = useState([]);
    const theme = useTheme();
    const balances = props.balances;
    const threshold = balances.threshold;
    const configuration = useContext(ConfigurationContext)[0];
    const headless = configuration.headless || false;
    const handleOrder = props.onOrder;

    let rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];

    const handleSelectAllClick = (event) => {
        if (selected.length === 0) {
            const newSelecteds = rows.map((n) => n.coin).filter(coin => coin !== configuration.basecoin);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };

    const handleClick = (event, coin) => {
        if (!headless && coin !== configuration.basecoin) {
            const selectedIndex = selected.indexOf(coin);
            let newSelected = [];

            if (selectedIndex === -1) {
                newSelected = newSelected.concat(selected, coin);
            } else if (selectedIndex === 0) {
                newSelected = newSelected.concat(selected.slice(1));
            } else if (selectedIndex === selected.length - 1) {
                newSelected = newSelected.concat(selected.slice(0, -1));
            } else if (selectedIndex > 0) {
                newSelected = newSelected.concat(
                    selected.slice(0, selectedIndex),
                    selected.slice(selectedIndex + 1),
                );
            }
            setSelected(newSelected);
        }
    };

    const isSelected = (coin) => selected.indexOf(coin) !== -1;
    let tooSmallAmount = false;

    return (
        <div className={classes.root}>
            <Paper className={classes.paper}>
                <EnhancedTableToolbar selected={selected} headless={headless} portfolio={rows} handleOrder={handleOrder} setSelected={setSelected} basecoin={configuration.basecoin} />
                <TableContainer>
                    <Table
                        className={classes.table}
                        size="small"
                    >
                        <EnhancedTableHead
                            classes={classes}
                            numSelected={selected.length}
                            onSelectAllClick={handleSelectAllClick}
                            rowCount={rows.length}
                            headless={headless}
                        />
                        <TableBody>
                            {rows.map((row, index) => {
                                const isItemSelected = isSelected(row.coin);
                                const labelId = `enhanced-table-checkbox-${index}`;
                                let sidechip = <div style={{ color: '#999999', width: '120px' }}>-</div>;
                                let tradelabel = formatResult(row.trade, 8);
                                let tradechip = <Chip label={tradelabel} style={{ backgroundColor: theme.palette.text.disabled, color: 'white', width: '120px' }} />;

                                let sidelabel = row.side;
                                if (row.reverse) sidelabel += ' (reverse)';

                                if (row.side === 'sell') {
                                    if (row.distribution / row.target - 1 >= threshold / 100) {
                                        sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.error.dark, color: 'white', width: '120px' }} />
                                        if (!row.valid) {
                                            tradechip = <Chip label={tradelabel} style={{ backgroundColor: theme.palette.warning.dark, color: 'white', width: '120px' }} />
                                            tooSmallAmount = true;
                                        }
                                    } else {
                                        sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.text.disabled, color: 'white', width: '120px' }} />
                                    }
                                } else if (row.side === 'buy') {
                                    if (row.distribution / row.target - 1 <= -threshold / 100) {
                                        sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.success.dark, color: 'white', width: '120px' }} />
                                        if (!row.valid) {
                                            tradechip = <Chip label={tradelabel} style={{ backgroundColor: theme.palette.warning.dark, color: 'white', width: '120px' }} />
                                            tooSmallAmount = true;
                                        }
                                    } else {
                                        sidechip = <Chip label={sidelabel} style={{ backgroundColor: theme.palette.text.disabled, color: 'white', width: '120px' }} />
                                    }
                                }

                                let checkbox;
                                if (!headless && row.coin !== configuration.basecoin) {
                                    checkbox = <Checkbox
                                        checked={isItemSelected}
                                        inputProps={{ 'aria-labelledby': labelId }}
                                    />
                                } else {
                                    checkbox = null;
                                }

                                return (
                                    <TableRow className={classes.tableRow}
                                        hover
                                        onClick={(event) => handleClick(event, row.coin)}
                                        role="checkbox"
                                        aria-checked={isItemSelected}
                                        tabIndex={-1}
                                        key={row.coin}
                                        selected={isItemSelected}
                                    >
                                        <TableCell padding="checkbox">
                                            {checkbox}
                                        </TableCell>
                                        <TableCell component="th" id={labelId} scope="row" padding="none">
                                            {row.coin}
                                        </TableCell>
                                        <TableCell align="right">{row.number}</TableCell>
                                        <TableCell align="right">{formatResult(row.last, 8)}</TableCell>
                                        <TableCell align="right">{formatResult(row.value, 8)}</TableCell>
                                        <TableCell align="right">{formatResult(row.distribution, 3)}</TableCell>
                                        <TableCell align="right">{formatResult(row.target, 3)}</TableCell>
                                        <TableCell align="right">{tradechip}</TableCell>
                                        <TableCell align="center">{sidechip}</TableCell>
                                    </TableRow>
                                );
                            })}
                        </TableBody>
                    </Table>
                </TableContainer>
                {(tooSmallAmount) && (
                    <Toolbar>
                        <Typography className={classes.title} variant="subtitle1" style={{ color: theme.palette.warning.dark }} component="div">
                            Trade amounts indicated in orange are too small to execute - consider increasing your balancing budget or hold fewer coins
                        </Typography>
                    </Toolbar>
                )}
            </Paper>
        </div>
    );
}