import React, { useContext, useState, useEffect, useRef } from 'react';
import { makeStyles, withStyles, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { Link, Switch, Route, Redirect, useHistory } from 'react-router-dom';
import Link2 from '@material-ui/core/Link';
import { generatePath } from "react-router";
import DMSwitch from '@material-ui/core/Switch';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Hidden from '@material-ui/core/Hidden';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Chip from '@material-ui/core/Chip';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import IconButton from '@material-ui/core/IconButton';
import { useLocalStore } from "../services/LocalStoreService";
import { FiatContext } from '../context/FiatContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ServerContext } from '../context/ServerContext';
import { BalancesContext } from '../context/BalancesContext';
import { isAfter } from 'date-fns';
import { useSnackbar } from 'notistack';
import { SnackbarProvider } from 'notistack';
import { routesUp, routesDown } from '../routes/Routes';
import { GraphQLClient, gql } from 'graphql-request';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import pckg from '../version.json';
import pckg2 from '../version2.json';
import ReactMarkdown from 'react-markdown';
import cr from 'crypto-js';
import { differenceInHours } from 'date-fns';
import { socket } from '../context/SocketContext';
import { Crypt } from 'hybrid-crypto-js';

const drawerWidth = 240;
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  logo: {
    maxWidth: 60,
    padding: 10,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    height: '100%',
  },
  drawerContainer: {
    overflow: 'auto',
  },
  settingsPaper: {
    width: '100%',
    height: '100%',
  },
  content: {
    justify: "flex-start",
    alignItems: "flex-start",
    flexGrow: 1,
    padding: theme.spacing(2),
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  footer: {
    // padding: theme.spacing(1),
    marginTop: 'auto',
    backgroundColor: '#000000',
  },
  bottomPush: {
    position: "fixed",
    width: '100%',
    bottom: 0,
    paddingBottom: 0,
    zIndex: theme.zIndex.drawer + 1,
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
  bottomPush2: {
    position: "fixed",
    bottom: 0,
    paddingBottom: 80,
  },
  smallScreenToggles: {
    maxWidth: '100%',
    bottom: 0,
    paddingBottom: 80,
    paddingTop: 10,
  },
}));

function Copyright() {
  return (
    <Box color="#FFF" height={54} p={2}>
      <Typography variant="body2" align="center">
        {'Copyright © '}
        <Link2 style={{ color: '#ffffff', textDecoration: 'none' }} href="https://hodlerhacks.com/" target="_blank" rel="noopener">
          hodlerhacks.com
        </Link2>{' '}
        {new Date().getFullYear()}
      </Typography>
    </Box>
  );
}

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    boxShadow: theme.shadows[1],
    fontSize: 12,
  },
}))(Tooltip);

export default function Main() {
  const history = useHistory();
  const version = pckg2.version;

  const [mode, setMode] = useLocalStore('mode', 'light');
  const [fiatStore, setFiatStore] = useLocalStore('fiat', 'EUR');
  const [lastMsg, setLastMsg] = useLocalStore('last-msg', 0);
  const [latestVersion, setLatestVersion] = useLocalStore('latest-version', version);
  const [fiat, setFiat] = useContext(FiatContext);
  const [configuration, setConfiguration] = useContext(ConfigurationContext);
  const [modus, setModus] = useState('manual');
  const [{ status, serverversion, servertime, access, publicKey, loginNow }, setServer] = useContext(ServerContext);
  const balances = useContext(BalancesContext)[0];
  const dv = cr.enc.Base64.parse(pckg.v).toString(cr.enc.Utf8);
  const elements = dv.split('.');
  const bbm = elements[0] + '.' + elements[1];
  const bbv = dv;
  const [outdated, setOutdated] = useState(false);
  const [open, setOpen] = useState(true);
  const [openDialog, setOpenDialog] = useState(false);
  const [disconnected, setDisconnected] = useState(false);
  const [dialog, setDialog] = useState({ number: 0, title: '', message: '', lock: false });
  const [messages, setMessages] = useState([]);
  const [maxWidth, setMaxWidth] = useState('sm');
  const [modusSwitchDisabled, setModusSwitchDisabled] = useState(window.location.pathname.includes('/configuration'));
  const [accessAllowed, setAccessAllowed] = useState(false);
  const [accessAllowedMsg, setAccessAllowedMsg] = useState('initial state');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [flexWidthDown, setFlexWidthDown] = useState(0);
  const [flexWidthUp, setFlexWidthUp] = useState(0);

  const getBotPath = () => {
    const pathname = window.location.pathname;
    const result = pathname.match(/bot[0-9]{3}/);

    if (result)
      return result[0];
    else
      return null;
  }

  const [botPath, setBotPath] = useState(getBotPath());

  const getPath = (page) => {
    if (botPath) {
      return generatePath("/:bot/:page", {
        bot: botPath,
        page: page,
      });
    } else {
      return '';
    }
  }

  let crypt = new Crypt({
    md: 'sha256',
    rsaStandard: 'RSA-OAEP',
  });

  let lastCheck = new Date();
  let pageLoaded = lastCheck;

  // Use a ref to access the current lastMsg value in an async callback
  const lastRef = useRef(lastMsg);
  lastRef.current = lastMsg;

  const getCountTimeout = () => {
    setTimeout(() => {
      getMessages(lastRef.current);
    }, 60 * 60 * 1000)
  }

  const handleCloseDialog = (e) => {
    const last = e.target.parentElement.id;
    setLastMsg(last);
    setOpenDialog(false);

    // Remove shown message from array
    const newMessages = messages.slice(1, messages.length);
    setMessages(newMessages);
  };

  const handleRefresh = () => {
    window.location.reload();
  }

  useEffect(() => {
    setDisconnected(status === 'disconnected' && Object.keys(configuration).length !== 0)

    return () => {
    }
  }, [status]);

  useEffect(() => {
    document.title = `BB | Bot Dashboard`;

    // Listen to local store changes (when toggled on another page)
    window.addEventListener('storage', () => {
      const newMode = localStorage.getItem('mode');
      setMode(newMode);
      const newFiat = localStorage.getItem('fiat');
      setFiat(newFiat);
    });

    return () => {
    }
  }, []);

  useEffect(() => {
    // Reload page on server startup, to make sure server and client are in sync (in case BB has been updated)
    if (status === 'starting') window.location.reload();

    setOpen(status !== 'connected' || Object.keys(configuration).length === 0);
    return () => {
    }
  }, [status, configuration, balances]);

  useEffect(() => {
    setModus(configuration.headless ? 'auto' : 'manual');

    return () => {
    }
  }, [configuration]);

  useEffect(() => {
    const unlisten = history.listen((location) => {
      setModusSwitchDisabled(location.pathname === '/configuration');
      setBotPath(getBotPath());
    });
    // Clean up the event listener
    return () => {
      unlisten();
    };
  }, [history])

  useEffect(() => {
    const outdated = (serverversion && (serverversion !== '0.0' && serverversion !== version));
    setOutdated(outdated);

    return () => {
    }
  }, [serverversion]);

  useEffect(() => {
    let wdtDown = 0;
    let wdtUp = 0;
    const update = latestVersion > version;
    const logout = access && access.token;

    if (update) {
      wdtDown += 140;
    }
    if (logout) {
      wdtDown += 50;
      wdtUp += 50;
    }

    setFlexWidthDown(wdtDown);
    setFlexWidthUp(wdtUp);

    return () => {
    }
  }, [latestVersion, version, access]);

  // // TEST 'Update Available' chip
  // useEffect(() => {
  //   setLatestVersion('2.2.0');

  //   return () => {
  //   }
  // }, []);

  const classes = useStyles();

  const handleDarkmode = (e) => {
    (mode === 'light') ? setMode('dark') : setMode('light');
  }

  const handleFiat = (e) => {
    if (fiat === 'EUR') {
      setFiat('USD');
      setFiatStore('USD');
    } else {
      setFiat('EUR');
      setFiatStore('EUR');
    }
  }

  const handleModus = (e) => {
    const newModus = (modus === 'auto') ? 'manual' : 'auto';
    const headless = (newModus === 'auto');
    setModus(newModus);
    if (socket && access.allow) socket.emit("store-headless", {
      headless: headless,
    }, access.token);
  }

  useEffect(() => {
    if (access) {
      if (access.allow === false && access.reason === '2FA required') {
        setAccessAllowed(false);
        setAccessAllowedMsg("Add your client's IP address to the IP whitelist, or temporarily allow access throught your Telegram bot");
      } else if (access.allow === false && access.reason === 'temporary access not allowed') {
        setAccessAllowed(false);
        setAccessAllowedMsg("Add your client's IP address to the IP whitelist");
      } else if (access.allow === false && access.reason === 'access timeout') {
        setAccessAllowed(false);
        setAccessAllowedMsg("Your access has timed out");
      } else if (access.allow === false && access.reason === '2FA login required') {
        setAccessAllowed(false);
        setAccessAllowedMsg("2FA required - approve login through your Telegram bot");
      } else if (access.allow === false && access.reason === 'invalid credentials') {
        setAccessAllowed(false);
        setAccessAllowedMsg("Incorrect username or password");
      } else if (access.allow === false && access.reason === 'logged out') {
        setAccessAllowed(false);
        setAccessAllowedMsg("You've logged out");
      } else if (access.allow === false && access.reason === 'initial state') {
        setAccessAllowed(false);
        setAccessAllowedMsg("initial state");
      } else if (access.allow === false && access.reason === 'reload') {
        handleRefresh();
      } else {
        setAccessAllowed(true);
      }
    }

    return () => {
    }
  }, [access]);

  const theme = createMuiTheme({
    palette: {
      type: mode
    }
  });

  const StyledButton = withStyles({
    root: {
      '&:hover': {
        backgroundColor: 'transparent',
      },
      '&:click': {
        backgroundColor: '#f00',
      },
      justifyContent: 'flex-start',
    },
  })(Button);

  useEffect(() => {
    if (messages.length > 0) {
      const msg = messages[0];
      if (msg.bbvNew) setLatestVersion(msg.bbvNew);

      // Check if bbvReceive is minor version number, if so, only show message in case it matches current version
      const count = (msg.bbvReceive.match(/\./g) || []).length;
      if (count !== 2 || msg.bbvReceive === bbv) {

        // Check if the message contains a new version number, and if so, if BB is already on the new version
        let alreadyUpdated = false;
        if (msg.bbvNew && bbv >= msg.bbvNew) alreadyUpdated = true;

        // If already on the latest version, don't show the message
        if (!alreadyUpdated) {
          if (msg.dialogWidth === null) {
            const textLength = msg.message.length;
            if (textLength < 400) setMaxWidth('sm');
            else if (textLength < 800) setMaxWidth('md');
            else if (textLength < 3000) setMaxWidth('lg');
            else setMaxWidth('xl');
          } else {
            setMaxWidth(msg.dialogWidth);
          }

          const newDialogMsg = {
            number: msg.number,
            title: msg.title,
            message: msg.message,
            width: msg.dialogWidth,
            lock: msg.lock,
            updated: alreadyUpdated,
          }

          setDialog(newDialogMsg);
          setOpenDialog(true)
        } else {
          setLastMsg(msg.number);

          // Remove shown message from array
          const newMessages = messages.slice(1, messages.length);
          setMessages(newMessages);
        }
      } else {
        // Remove message from array
        const newMessages = messages.slice(1, messages.length);
        setMessages(newMessages);
      }
    }
  }, [messages]);

  async function getMessages(last) {
    // Check if 4h past since last check
    const now = new Date();
    const diff = differenceInHours(now, lastCheck);
    if (lastCheck !== pageLoaded && diff < 4) {
      getCountTimeout();
    } else {
      lastCheck = new Date();
      const endpoint = 'https://api-eu-central-1.graphcms.com/v2/ckoytzv3lotky01w59glace7y/master';
      const graphQLClient = new GraphQLClient(endpoint);
      const headers = {
        authorization: 'Bearer eyJhbGciOiJSUzI1NiIsInR5cCI6IkpXVCIsImtpZCI6ImdjbXMtbWFpbi1wcm9kdWN0aW9uIn0..cEJ5isSubzX1ymlpkfyvfgZ6zyUMFiFOVaD4N84NoEOxwGZ8DtYM1k4DlCjoeCcTR8Ftr43bIGDxR4apANAG_-wf9Nge9T2MAuKa9z2sCWA5jnYtTHYU-x1Hx_ShpHAl1wPjrSpTMquV3bRlKQBm6EQTreoaQZgBXY25ClLbVzXFcohcDn3RGi3hHVRvxNLr0F7ETwFJnbowm9UX66vz2SP-5-u-CBKUxSipKMO5za0MZiIWoprSdE0f22xVXp8up3k_6-5x22mS88P2OFgMXoA-UBFS8nz6jRE_fRULzCouKYyJG3oQ10ovuZ1M729GQ0rMv9fkJroVKyS4i8KgglwPPBwQbaD2LCJXINKXYC65DmKyJ9UjWxQ9Lv6akcnkT7CkeqEqzwLnCEQgZEmJS_PBFDu40LMS8UbnwDcntJznA8rH_jsD7IkMdyRyXmns6fbLOc91Q1idBCk-VqUzkL0FzfscCVpkg1J7kGtGeW4YEWhWyPze4ZO4IxM9WRoSsCDUOx3EXklA0WSdR2M6IRnoFL-qoawOLQaaPACy8-QGmaAL6N6fuMIHUWavOTYnyLip0oyy74sUHz4S4sx4mAf5pexlvDcxM_6RIRnCQ84THsM2KwpoWkpR-F2s4pIyfYYn-4R7MNENfPGxNm4INjBYrCOWyZN_vXHGBa4uw2U',
      }
      const variables = {
        last: parseInt(last),
        bbm: bbm,
      }

      const query = gql`
        query getMessages($bbm: String!, $last: Int!) {
              messages(where: {
                number_gt: $last
                OR: [
                  {bbvReceive: "any"},
                  {bbvReceive_contains: $bbm}
                ]
                archived_not: true
              }, orderBy: number_ASC) {
                id
                number
                bbvReceive
                bbvNew
                title
                message
                dialogWidth
                lock
              }
            }
        `

      try {
        const data = await graphQLClient.request(query, variables, headers);
        setMessages(data.messages);
      } catch (err) {
        // console.error(JSON.stringify(err, undefined, 2));
      }

      getCountTimeout();
    }
  }

  useEffect(() => {
    getMessages(lastMsg);
  }, []);

  // const timebomb = isAfter(new Date(), new Date(2021, 7, 1));   // After 2021/08/01

  // useEffect(() => {
  //   if (timebomb) {
  //     enqueueSnackbar("Please update Balance Bot -- In the meanwhile, your bot keeps running", {
  //       variant: 'error',
  //       autoHideDuration: null,
  //     });
  //   }
  // }, []);

  const onLoginInput = (e) => {
    if (e.target.id === 'username') setUsername(e.target.value);
    if (e.target.id === 'password') setPassword(e.target.value);
  }

  const onSignIn = () => {
    const timestamp = new Date().getTime();

    if (socket) socket.emit("login", {
      username: username,
      password: crypt.encrypt(publicKey, `${password}@${timestamp}`),
    });
  }

  const onLogout = () => {
    if (socket) socket.emit("logout", access.token);
    setServer({
      status: status,
      serverversion: serverversion,
      servertime: servertime,
      access: {
        allow: false,
        reason: 'logged out',
        token: null,
      },
      publicKey: publicKey,
      loginNow: loginNow,
    });
  };

  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  const action = key => (
    <React.Fragment>
      <Button
        style={{
          color: '#bbbbbb',
        }}
        onClick={() => { closeSnackbar(key) }}>
        Dismiss
      </Button>
    </React.Fragment>
  );

  const handleForgotPassword = () => {
    if (socket) socket.emit("reset-password");
    enqueueSnackbar('See your Telegram bot to reset password', {
      variant: 'success',
      autoHideDuration: 8000,
      action,
    });
  }

  return (<ThemeProvider theme={theme}>
    {disconnected ? (
      <Dialog
        open={true}
        fullWidth={true}
        maxWidth={'sm'}
      >
        <DialogContent>
          <DialogContentText>
            <Typography variant="overline" color="secondary">
              Disconnected from server
            </Typography>
          </DialogContentText>
          <DialogContentText>
            <Typography variant="overline">
              The connection with the server has been lost. You may have opened the application from another browser or device, or the bot has stopped running.
            </Typography>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleRefresh} color="secondary">
            Reconnect
          </Button>
        </DialogActions>
      </Dialog>
    ) : (!accessAllowed && accessAllowedMsg === 'initial state') ? (<div></div>) :
      (!outdated && accessAllowed && !loginNow) ? (
        <React.Fragment>
          <Backdrop className={classes.backdrop} open={open}>
            <CircularProgress color="inherit" />
          </Backdrop>
          <Dialog
            open={openDialog}
            fullWidth={true}
            maxWidth={maxWidth}
          >
            <DialogTitle id="dialog-title">{dialog.title}</DialogTitle>
            <DialogContent>
              <DialogContentText id="dialog-description">
                {/* {(dialog.updated) && (
              <Typography variant="overline" color="secondary">
                You are already on the latest version
              </Typography>
            )} */}
                <ReactMarkdown linkTarget="_blank">
                  {dialog.message}
                </ReactMarkdown>
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button variant="contained" id={dialog.number} onClick={handleCloseDialog} color="secondary" disabled={dialog.lock}>
                Close
              </Button>
            </DialogActions>
          </Dialog>
          {/* <BrowserRouter> */}
          <div className={classes.root}>
            <CssBaseline />
            <AppBar position="fixed" className={classes.appBar}>
              <Toolbar>
                <img src="/logo192.png" alt="Balance Bot Logo" className={classes.logo} />
                <Grid container direction="row" alignItems="center" nowrap="true">
                  <Grid item xs zeroMinWidth>
                    <Box mt={1.5}>
                      <Typography variant="h6" color="inherit" noWrap>
                        Balance Bot | {configuration.botname}
                      </Typography>
                    </Box>
                    <Box mt={-1} style={{ color: '#959FD6' }}>
                      <Typography variant="caption" color="inherit" noWrap>
                        v{version}
                      </Typography>
                    </Box>
                  </Grid>
                  <Hidden xsDown>
                    <FlexToolbar wdt={flexWidthDown} update={latestVersion > version} logout={access && access.token} onLogout={onLogout} />
                  </Hidden>
                  <Hidden smUp>
                    <FlexToolbar wdt={flexWidthUp} update={latestVersion > version} logout={access && access.token} onLogout={onLogout} />
                  </Hidden>
                </Grid>
              </Toolbar>
            </AppBar>
            <Hidden mdDown>
              <Drawer className={classes.drawer} variant="permanent">
                <Paper className={classes.drawerPaper}>
                  <Grid container direction="column">
                    <Grid item xs={12}>
                      <Toolbar />
                      {(configuration.apiKey !== '') ? (
                        <div className={classes.drawerContainer}>
                          <List>
                            {routesUp.map((prop, key) => {
                              return (
                                <Link to={getPath(prop.path)} style={{ textDecoration: 'none' }} key={key}>
                                  <ListItem button key={prop.sidebarName}>
                                    <ListItemIcon><prop.icon /></ListItemIcon>
                                    <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                                  </ListItem>
                                </Link>
                              );
                            })}
                          </List>
                          <Divider />
                          {routesDown.map((prop, key) => {
                            return (
                              <Link to={getPath(prop.path)} style={{ textDecoration: 'none' }} key={key}>
                                <ListItem button key={prop.sidebarName}>
                                  <ListItemIcon><prop.icon /></ListItemIcon>
                                  <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                                </ListItem>
                              </Link>
                            );
                          })}
                        </div>
                      ) : (
                        <div className={classes.drawerContainer}>
                          <List>
                            {routesUp.map((prop, key) => {
                              return (
                                <Redirect to={getPath('configuration')} style={{ textDecoration: 'none' }} key={key}>
                                  <ListItem button key={prop.sidebarName}>
                                    <ListItemIcon><prop.icon /></ListItemIcon>
                                    <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                                  </ListItem>
                                </Redirect>
                              );
                            })}
                          </List>
                          <Divider />
                          {routesDown.slice(0, 1).map((prop, key) => {
                            return (
                              <Link to={getPath(prop.path)} style={{ textDecoration: 'none' }} key={key}>
                                <ListItem button key={prop.sidebarName}>
                                  <ListItemIcon><prop.icon /></ListItemIcon>
                                  <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                                </ListItem>
                              </Link>
                            );
                          })}
                          {routesDown.slice(1, routesDown.length).map((prop, key) => {
                            return (
                              <Redirect to={getPath('configuration')} style={{ textDecoration: 'none' }} key={key}>
                                <ListItem button key={prop.sidebarName}>
                                  <ListItemIcon><prop.icon /></ListItemIcon>
                                  <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                                </ListItem>
                              </Redirect>
                            );
                          })}
                        </div>
                      )}
                    </Grid>
                    <div className={classes.bottomPush2}>
                      <Box pl={1}>
                        <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ width: '200px' }}>
                          <Grid item xs={8}>
                            <FormControlLabel
                              onChange={handleDarkmode}
                              value={mode}
                              control={<DMSwitch checked={mode === 'dark'} color="secondary" />}
                              label={<Typography variant="button">Light&nbsp;&nbsp;&nbsp;&nbsp;</Typography>}
                              labelPlacement="start"
                            />
                          </Grid>
                          <Grid item xs={4}>
                            <StyledButton disableRipple onClick={handleDarkmode}><Typography variant="button">Dark</Typography></StyledButton>
                          </Grid>
                        </Grid>
                        <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ width: '200px' }}>
                          <Grid item xs={8}>
                            <FormControlLabel
                              onChange={handleFiat}
                              value={fiat}
                              control={<DMSwitch checked={fiat === 'EUR'} color="secondary" />}
                              label={<Typography variant="button">USD&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>}
                              labelPlacement="start"
                            />
                          </Grid>
                          <Grid item xs={4}>
                            <StyledButton disableRipple onClick={handleFiat}><Typography variant="button">EUR</Typography></StyledButton>
                          </Grid>
                        </Grid>
                        <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ width: '200px' }}>
                          <Grid item xs={8}>
                            <FormControlLabel
                              disabled={modusSwitchDisabled}
                              onChange={handleModus}
                              value={modus}
                              control={<DMSwitch checked={modus === 'auto'} color="secondary" />}
                              label={<Typography variant="button">Manual</Typography>}
                              labelPlacement="start"
                            />
                          </Grid>
                          <Grid item xs={4}>
                            <StyledButton disableRipple onClick={handleModus}><Typography variant="button">Auto</Typography></StyledButton>
                          </Grid>
                        </Grid>
                      </Box>
                    </div>
                  </Grid>
                </Paper>
              </Drawer>
            </Hidden>
            <main className={classes.content}>
              <Toolbar />
              <Switch>
                <Route exact path={'/:bot'}>
                  {(configuration.apiKey !== '') ? (
                    <Redirect to={getPath('portfolio')} />
                  ) : (
                    <Redirect to={getPath('configuration')} />
                  )}
                </Route>
                {routesUp.map((prop, key) => {
                  return (
                    <Route exact path={'/:bot/' + prop.path} component={prop.component} key={key} />
                  );
                })}
                {routesDown.map((prop, key) => {
                  return (
                    <Route exact path={'/:bot/' + prop.path} component={prop.component} key={key} />
                  );
                })}
              </Switch>
              <Hidden lgUp>
                <div className={classes.smallScreenToggles}>
                  <Paper className={classes.settingsPaper}>
                    <Box pl={1}>
                      <Grid container direction="column">
                        <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ maxWidth: '220px' }}>
                          <Grid item xs={7}>
                            <FormControlLabel
                              onChange={handleDarkmode}
                              value={mode}
                              control={<DMSwitch checked={mode === 'dark'} color="secondary" />}
                              label={<Typography variant="button">Light&nbsp;&nbsp;&nbsp;&nbsp;</Typography>}
                              labelPlacement="start"
                            />
                          </Grid>
                          <Grid item xs={3}>
                            <StyledButton disableRipple onClick={handleDarkmode}><Typography variant="button">Dark</Typography></StyledButton>
                          </Grid>
                          <Grid item xs={2}></Grid>
                        </Grid>
                        <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ maxWidth: '220px' }}>
                          <Grid item xs={7}>
                            <FormControlLabel
                              onChange={handleFiat}
                              value={fiat}
                              control={<DMSwitch checked={fiat === 'EUR'} color="secondary" />}
                              label={<Typography variant="button">USD&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>}
                              labelPlacement="start"
                            />
                          </Grid>
                          <Grid item xs={3}>
                            <StyledButton disableRipple onClick={handleFiat}><Typography variant="button">EUR</Typography></StyledButton>
                          </Grid>
                          <Grid item xs={2}></Grid>
                        </Grid>
                        <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ maxWidth: '220px' }}>
                          <Grid item xs={7}>
                            <FormControlLabel
                              disabled={modusSwitchDisabled}
                              onChange={handleModus}
                              value={modus}
                              control={<DMSwitch checked={modus === 'auto'} color="secondary" />}
                              label={<Typography variant="button">Manual</Typography>}
                              labelPlacement="start"
                            />
                          </Grid>
                          <Grid item xs={3}>
                            <StyledButton disableRipple onClick={handleModus}><Typography variant="button">Auto</Typography></StyledButton>
                          </Grid>
                          <Grid item xs={2}></Grid>
                        </Grid>
                      </Grid>
                    </Box>
                  </Paper>
                </div>
              </Hidden>
            </main>
          </div>
          {/* </BrowserRouter> */}
        </React.Fragment>
      ) : (accessAllowed && !loginNow) ? (
        <Dialog
          open={true}
          fullWidth={true}
          maxWidth={'sm'}
        >
          <DialogContent>
            <DialogContentText>
              <Typography variant="overline" color="secondary">
                New version has been installed
              </Typography>
              <DialogContentText>
              </DialogContentText>
              <Typography variant="overline">
                Please refresh the browser window to load the latest version
              </Typography>
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button variant="contained" onClick={handleRefresh} color="secondary">
              Refresh
            </Button>
          </DialogActions>
        </Dialog>
      ) : !loginNow ? (
        <Dialog
          open={true}
          fullWidth={true}
          maxWidth={'sm'}
        >
          <DialogContent>
            <DialogContentText>
              <Typography variant="overline" color="secondary">
                Access Denied
              </Typography>
            </DialogContentText>
            <DialogContentText>
              <Typography variant="overline">
                {accessAllowedMsg}
              </Typography>
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button variant="contained" onClick={handleRefresh} color="secondary">
              Back
            </Button>
          </DialogActions>
        </Dialog>
      ) : (
        <React.Fragment>
          <CssBaseline />
          <Dialog
            open={true}
            fullWidth={true}
            maxWidth={'sm'}
          >
            <DialogTitle>Log In</DialogTitle>
            <DialogContent>
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="username"
                label="Username"
                name="username"
                onChange={onLoginInput}
              />
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                onChange={onLoginInput}
              />
              <Box m={3} />
              <Button
                fullWidth
                variant="contained"
                color="secondary"
                onClick={onSignIn}
              >
                Log In
              </Button>
              <Box m={3} />
              <Button fullWidth onClick={handleForgotPassword} color="default" style={{ fontSize: '10px' }}>
                Forgot password
              </Button>
            </DialogContent>
          </Dialog>
        </React.Fragment>
      )
    }
    <div className={classes.bottomPush}>
      <footer className={classes.footer}>
        <Grid container alignItems="center">
          <Grid item xs={false} sm={12} md={12}>
            <Copyright />
          </Grid>
        </Grid>
      </footer>
    </div>
  </ThemeProvider >
  )
}

const FlexToolbar = (props) => {
  const { wdt, update, logout, onLogout } = props;

  return (
    <Grid item xs container direction="row" alignItems="center" justify="flex-end" style={{ maxWidth: wdt }}>
      {(update) && (
        <Hidden xsDown>
          <Grid item style={{ width: '130px' }}>
            <Chip label='Update Available' style={{ backgroundColor: '#959FD6', color: 'white' }} />
          </Grid>
        </Hidden>
      )}
      {(logout) && (
        <Grid item style={{ width: '48px' }}>
          <LightTooltip title="Logout">
            <IconButton onClick={onLogout}>
              <ExitToAppIcon style={{ color: '#ffffff' }} />
            </IconButton>
          </LightTooltip>
        </Grid>
      )}
    </Grid>
  );
}