import React, { useContext, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TimelineView from "../components/TimelineView";
import StatusView from "../components/StatusView";
import LineChart from '../components/LineChart';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import { StartingBalanceContext } from '../context/StartingBalanceContext';
import { FiatContext } from '../context/FiatContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { socket } from '../context/SocketContext';
import { ServerContext } from '../context/ServerContext';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const Performance = () => {
  const classes = useStyles();

  const balances = useContext(BalancesContext)[0];
  const performance = useContext(PerformanceContext)[0];
  const startingBalance = useContext(StartingBalanceContext)[0];
  const fiat = useContext(FiatContext)[0];
  const configuration = useContext(ConfigurationContext)[0];
  const [data1, setData1] = useState({});
  const [data2, setData2] = useState({});
  const { access } = useContext(ServerContext)[0];
  const [open, setOpen] = useState(true);

  useEffect(() => {
    if (socket && startingBalance.length === 0 && access.allow) socket.emit("get-starting-balances", access.token);
    return () => {
    }
  }, []);

  useEffect(() => {
    setOpen(Object.keys(balances).length === 0);
    return () => {
    }
  }, [balances]);

  let values = [];
  let fiatValues = [];
  let change = [];
  let fiatChange = [];
  let netvalues = [];
  let netchange = [];
  let netvaluesfiat = [];
  let netchangefiat = [];
  let newdata1 = {};
  let newdata2 = {};
  let dayOne = [];

  useEffect(() => {
    computeGraphs();

    return () => {
    }
  }, [balances, performance, startingBalance, fiat]);

  const computeGraphs = () => {
    const rows = (balances.portfolio) ? JSON.parse(balances.portfolio) : [];

    if (startingBalance.coins) rows.forEach((coin, i) => {
      let c = startingBalance.coins.find(c => c.coin === coin.coin);
      dayOne[i] = {
        coin: coin.coin,
        initial: (c && c.coin) ? c.number : 0,
      };
    });
    else rows.forEach((coin, i) => {
      dayOne[i] = {
        coin: coin.coin,
        initial: 0,
      };
    });

    let initialValue = 0;
    let initialFiatValue = 0;

    if (startingBalance.length !== 0) {
      initialValue = startingBalance.basecoin;
      initialFiatValue = (fiat === 'EUR') ? startingBalance.eur : startingBalance.usd;

      if (performance.length !== 0) {
        performance.forEach((day) => {
          let value = 0;
          let fiatValue = 0;
          day.coins.forEach((coin) => {
            value += coin.number * coin.price;

            if (configuration.basecoin === 'BTC') {
              fiatValue += coin.number * coin.price * ((fiat === 'EUR') ? day.eur : day.usd);
            } else if (configuration.basecoin === 'BUSD' || configuration.basecoin === 'USDT' || configuration.basecoin === 'USDC') {
              fiatValue += coin.number * coin.price * ((fiat === 'EUR') ? day.eur / day.usd : 1);
            } else if (configuration.basecoin === 'EUR') {
              fiatValue += coin.number * coin.price * ((fiat === 'EUR') ? 1 : day.usd / day.eur);
            }
          });

          let netvalue = 0;
          let netvaluefiat = 0;
          day.coins.forEach((coin) => {
            // Skip coins that are not in current portfolio
            if (dayOne.find(c => c.coin === coin.coin)) {
              // In case starting balance is not set for a coin, find the first occurence in the performance db to set the initial price
              // CHECK: shouldn't be needed anymore, as starting balance should be set for all coins, also when added later
              if (dayOne.find(c => c.coin === coin.coin).initial === 0) dayOne.find(c => c.coin === coin.coin).initial = coin.number;
              netvalue += dayOne.find(c => c.coin === coin.coin).initial * coin.price;

              if (configuration.basecoin === 'BTC') {
                netvaluefiat += dayOne.find(c => c.coin === coin.coin).initial * coin.price * ((fiat === 'EUR') ? day.eur : day.usd);
              } else if (configuration.basecoin === 'BUSD' || configuration.basecoin === 'USDT' || configuration.basecoin === 'USDC') {
                netvaluefiat += dayOne.find(c => c.coin === coin.coin).initial * coin.price * ((fiat === 'EUR') ? day.eur / day.usd : 1);
              } else if (configuration.basecoin === 'EUR') {
                netvaluefiat += dayOne.find(c => c.coin === coin.coin).initial * coin.price * ((fiat === 'EUR') ? 1 : day.usd / day.eur);
              }
            }
          });
          values.push({
            x: day.date,
            y: value,
          });
          change.push({
            x: day.date,
            y: (value / initialValue - 1) * 100,
          });
          fiatValues.push({
            x: day.date,
            y: fiatValue,
          });
          fiatChange.push({
            x: day.date,
            y: (fiatValue / initialFiatValue - 1) * 100,
          });
          netvalues.push({
            x: day.date,
            y: value - netvalue,
          });
          netchange.push({
            x: day.date,
            y: ((value - netvalue) / initialValue) * 100,
          });
          netvaluesfiat.push({
            x: day.date,
            y: fiatValue - netvaluefiat,
          });
          netchangefiat.push({
            x: day.date,
            y: ((fiatValue - netvaluefiat) / initialFiatValue) * 100,
          });
        });
        newdata1.basecoin = [
          {
            id: `Total Value (${configuration.basecoin})`,
            data: values,
          }
        ];
        newdata2.basecoin = [
          {
            id: `${configuration.basecoin} Change (%)`,
            data: change,
          }
        ];
        newdata1.fiat = [
          {
            id: `Total Value (${fiat})`,
            data: fiatValues,
          }
        ];
        newdata2.fiat = [
          {
            id: `${fiat} Change (%)`,
            data: fiatChange,
          }
        ];
        newdata1.net = [
          {
            id: `Net Balancing Effect (${configuration.basecoin})`,
            data: netvalues,
          }
        ];
        newdata2.net = [
          {
            id: 'Net Balancing Effect (%)',
            data: netchange,
          }
        ];
        newdata1.netfiat = [
          {
            id: `Net Balancing Effect (${fiat})`,
            data: netvaluesfiat,
          }
        ];
        newdata2.netfiat = [
          {
            id: 'Net Balancing Effect (%)',
            data: netchangefiat,
          }
        ];
        setData1(newdata1);
        setData2(newdata2);
      }
    }
  }

  return (
    <div className={classes.root}>
      <Backdrop className={classes.backdrop} open={open}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <Grid container spacing={3}>
        <Grid item xs={6}>
          <Typography variant="h3" noWrap>
            Performance
          </Typography>
        </Grid>
        <Grid item xs={3}>
          <TimelineView data={performance} />
        </Grid>
        <Grid item xs={3}>
          <StatusView />
        </Grid>
        <Grid item xs={6}>
          <Paper style={{ height: '360px' }} className={classes.paper}>
            <LineChart data1={data1.basecoin} data2={data2.basecoin} type={'basecoin'} />
          </Paper>
        </Grid>
        <Grid item xs={6}>
          <Paper style={{ height: '360px' }} className={classes.paper}>
            <LineChart data1={data1.fiat} data2={data2.fiat} type={'fiat'} />
          </Paper>
        </Grid>
        <Grid item xs={6}>
          <Paper style={{ height: '360px' }} className={classes.paper}>
            <LineChart data1={data1.net} data2={data2.net} type={'net-result'} />
          </Paper>
        </Grid>
        <Grid item xs={6}>
          <Paper style={{ height: '360px' }} className={classes.paper}>
            <LineChart data1={data1.netfiat} data2={data2.netfiat} type={'net-result-fiat'} />
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}

export default Performance;