import React, { useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import { DataGrid } from '@material-ui/data-grid';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import { format } from 'date-fns';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    DatePicker,
} from '@material-ui/pickers';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';
import IconButton from '@material-ui/core/IconButton';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import { ServerContext } from '../context/ServerContext';

const columns = [
    { field: 'coin', headerName: 'Coin', width: 350, editable: false },
    { field: 'startnumber', headerName: 'Number', type: 'number', width: 200, editable: true, valueFormatter: ({ value }) => (Number(value)) },
    { field: 'startprice', headerName: 'Price', type: 'number', width: 200, editable: true, valueFormatter: ({ value }) => (Number(value)) },
];

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    header: {
        "& .MuiDataGrid-colCell":
            theme.palette.type === 'light'
                ? {
                    backgroundColor: '#aaaaaa',
                }
                : {
                    backgroundColor: '#777777',
                },
    },
    paper: {
        width: '100%',
        marginBottom: theme.spacing(2),
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
}));

export default function ConfigP3(props) {
    const classes = useStyles();

    let newStartPerformance = [];

    const [input, setInput] = useContext(ConfigurationInputContext);
    const newPortfolio = input.portfolio;
    const [portfolio, setPortfolio] = useState(newPortfolio);
    const [selectedDate, setSelectedDate] = useState(input.startdate);
    const [recreate, setRecreate] = useState(input.recreate);
    const [open, setOpen] = useState(false);
    const { access } = useContext(ServerContext)[0];

    const handleClose = () => {
        setOpen(false);
    };

    newPortfolio.forEach((coin) => {
        if (!coin.excluded) {
            newStartPerformance.push({
                id: coin.coin,
                coin: coin.coin,
                startnumber: Number((coin.startnumber).toFixed(8)),
                startprice: Number((coin.startprice).toFixed(8)),
            });
        }
    });

    const [startPerformance, setStartPerformance] = useState(newStartPerformance);

    const handleDateChange = (input) => {
        setSelectedDate(input);
    };

    const acceptDateChange = (input) => {
        setSelectedDate(input);
        const date = format(input, "yyyy/MM/dd");
        props.callback({
            field: 'startdate',
            input: date,
        });
    };

    const handleCheck = (e) => {
        const checked = e.target.checked;
        props.callback({
            field: 'recreate',
            input: checked,
        });
        setRecreate(checked);
    }

    const handleEditCellChangeCommitted = (e) => {
        let newPortfolio = [...portfolio];
        let match = newPortfolio.find(c => c.coin === e.id);
        match[e.field] = Number(e.props.value);

        setPortfolio(newPortfolio);
        props.callback({
            field: 'portfolio',
            input: newPortfolio,
        });
    }

    const handleButton = () => {
        newStartPerformance = [];
        portfolio.forEach((coin) => {
            newStartPerformance.push({
                id: coin.coin,
                coin: coin.coin,
                startnumber: Number((coin.number).toFixed(8)),
                startprice: Number((coin.price).toFixed(8)),
            });
        });
        setStartPerformance(newStartPerformance);

        let newPortfolio = [...portfolio];
        newPortfolio.forEach((row) => {
            let match = newStartPerformance.find(c => c.coin === row.coin);
            row.startnumber = match.startnumber;
            row.startprice = match.startprice;
        })
        setPortfolio(newPortfolio);
        props.callback({
            field: 'portfolio',
            input: newPortfolio,
        });
    }

    function CustomToolbar() {
        return (
            <Grid container alignItems="flex-end">
                <Grid item xs={12}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DatePicker
                            disableToolbar="true"
                            variant="inline"
                            format="yyyy/MM/dd"
                            margin="normal"
                            id="date-picker-inline"
                            label="Set start date"
                            value={selectedDate}
                            disableFuture="true"
                            onChange={handleDateChange}
                            onAccept={acceptDateChange}
                        />
                    </MuiPickersUtilsProvider>
                </Grid>
                <Grid container alignItems="center" justify="space-between">
                    <Grid item>
                        <FormControlLabel
                            control={<Checkbox color="secondary" name="rewrite" value="yes" checked={recreate} onChange={handleCheck} />}
                            label="Recreate performance history"
                        />
                    </Grid>
                    <Grid item>
                        <Button variant="contained" color="primary" startIcon={<ArrowDownwardIcon />} onClick={handleButton}>
                            Use current balance
                        </Button>
                    </Grid>
                </Grid>
            </Grid>
        );
    }

    const [openTT, setOpenTT] = useState(false);

    const handleTooltipClose = () => {
        setOpenTT(false);
    };

    const handleTooltipOpen = () => {
        setOpenTT(true);
    };

    return (
        <div>
            <Backdrop className={classes.backdrop} open={open}>
                <CircularProgress color="inherit" />
            </Backdrop>
            <React.Fragment>
                <Grid container alignItems="flex-end">
                    <Grid item xs={11}>
                        <Typography variant="h6" gutterBottom>
                            Starting Balance
                        </Typography>
                    </Grid>
                    <Grid item xs={1}>
                        <ClickAwayListener onClickAway={handleTooltipClose}>
                            <HtmlTooltip
                                title={
                                    <React.Fragment>
                                        <Box m={2}>
                                            <div><b>{"Use of starting balance"}</b></div>
                                            <div>{"Balance Bot uses the starting balance to determine the performance of your balancing portfolio. It compares the current portfolio value with the starting portfolio value to calculate the change."}</div>
                                            <div>&nbsp;</div>
                                            <div><b>{"Default starting balance"}</b></div>
                                            <div>{"By default, Balance Bot will check the number of coins in your account and their prices upon first-time configuration, and use these to calculate the starting balance."}</div>
                                            <div>&nbsp;</div>
                                            <div><b>{"Customize starting balance"}</b></div>
                                            <div>{"If you started balancing at an earlier point in time, you can manually set the starting balance for your account. Do so by filling in the number of coins and their prices (expressed in your selected base coin) at which you purchased those coins. Simply click on the cells in the table to edit."}</div>
                                            <div>&nbsp;</div>
                                            <div>{"Alternatively, when clicking the 'use current balance' button, Balance Bot will use the current balance to fill the table. This may be useful if you've added or removed crypto to/from your portfolio and you want to reset the performance measurements."}</div>
                                            <div>&nbsp;</div>
                                            <div><b>{"Recreate performance history"}</b></div>
                                            <div>{"Balance Bot can 'recreate history' by analyzing trades that were executed as of the start date, and use that information to create performance graphs for that period. Tick the 'Recreate performance history' checkbox to confirm that you want to do so. Make sure to set the start date right "}<b>{"after"}</b>{" the date when you bought the coins in your portfolio, to avoid these purchases to be counted twice."}</div>
                                            <div>&nbsp;</div>
                                            <div>{"Depending on the start date and your trade history, it may take several minutes to recreate your performance history and until this is visible on the performance screen. You will receive a notification message once the process has completed."}</div>
                                            <div>&nbsp;</div>
                                            <div><i>{"Note: "}</i>{"Recreating the performance history will also recreate your event history, including all executed orders since the start date, but excluding error messages that may have been logged in the past."}</div>
                                        </Box>
                                    </React.Fragment>
                                }
                                onClose={handleTooltipClose}
                                open={openTT}
                                disableFocusListener
                                disableHoverListener
                                disableTouchListener
                            >
                                <IconButton onClick={openTT ? handleTooltipClose : handleTooltipOpen}>
                                    <InfoOutlinedIcon color="secondary" />
                                </IconButton>
                            </HtmlTooltip>
                        </ClickAwayListener>
                    </Grid>
                </Grid>
                <div style={{ width: '752px' }}>
                    <CustomToolbar />
                    <Box pt={3}>
                        <Grid container>
                            <Paper className={classes.paper} elevation={0}>
                                <div style={{ width: '752px' }}>
                                    <DataGrid className={classes.header} rows={startPerformance} columns={columns}
                                        autoHeight={true}
                                        density='compact'
                                        disableColumnSelector={true}
                                        disableColumnMenu={true}
                                        hideFooter={true}
                                        hideFooterPagination={true}
                                        hideFooterRowCount={true}
                                        onEditCellChangeCommitted={handleEditCellChangeCommitted}
                                    />
                                </div>
                            </Paper>
                        </Grid>
                    </Box>
                </div>
            </React.Fragment>
        </div>
    );
}