import React, { useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { ConfigurationInputContext } from '../context/ConfigurationInputContext';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import { HtmlTooltip } from './HtmlToolTip';
import IconButton from '@material-ui/core/IconButton';
import Box from '@material-ui/core/Box';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';

const useStyles = makeStyles((theme) => ({
    formControl: {
        minWidth: '100%',
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    textinput: {
        width: '380px',
    }
}));

export default function ConfigP4(props) {
    const classes = useStyles();

    const [input, setInput] = useContext(ConfigurationInputContext);

    const [botname, setBotname] = useState(input.botname || '');
    const [threshold, setThreshold] = useState(input.threshold || '3');
    const [ordertype, setOrdertype] = useState(input.ordertype || 'market');
    const [timeout, setTimeout] = useState(input.timeout || 20);
    const [headless, setHeadless] = useState(input.headless || false);
    const [cooldown, setCooldown] = useState(input.cooldown || 0);

    const onHeadless = (e) => {
        props.callback({
            field: 'headless',
            input: e.target.checked,
        });
        setHeadless(e.target.checked);
    }

    const onBotname = (e) => {
        props.callback({
            field: e.target.id,
            input: e.target.value
        });
        setBotname(e.target.value);
    }

    const onThreshold = (e) => {
        const pattern = /^[0-9]*\.?[0-9]*$/
        if (pattern.test(e.target.value)) {
            props.callback({
                field: e.target.id,
                input: e.target.value
            });
            setThreshold(e.target.value);
        }
    }

    const onOrdertype = (e) => {
        props.callback({
            field: e.target.name,
            input: e.target.value
        });
        setOrdertype(e.target.value);
    }

    const onTimeout = (e) => {
        props.callback({
            field: e.target.name,
            input: e.target.value
        });
        setTimeout(e.target.value);
    }

    const onCooldown = (e) => {
        const pattern = /^[0-9]*$/
        if (pattern.test(e.target.value)) {
            props.callback({
                field: e.target.id,
                input: e.target.value
            });
            setCooldown(e.target.value);
        }
    }

    const [open, setOpen] = useState(false);

    const handleTooltipClose = () => {
        setOpen(false);
    };

    const handleTooltipOpen = () => {
        setOpen(true);
    };

    return (
        <React.Fragment>
            <Grid container alignItems="flex-end">
                <Grid item xs={11}>
                    <Typography variant="h6" gutterBottom>
                        General Settings
                    </Typography>
                </Grid>
                <Grid item xs={1}>
                    <ClickAwayListener onClickAway={handleTooltipClose}>
                        <HtmlTooltip
                            title={
                                <React.Fragment>
                                    <Box m={2}>
                                        <div><b>{"Bot Name"}</b></div>
                                        <div>{"Give your bot a descriptive name. This is especially useful if you run multiple bots, so you can easily recognize each."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Balancing Threshold"}</b></div>
                                        <div>{"The balancing threshold is used by Balance Bot to determine when to sell/buy coins. The threshold is the relative deviation from the target distribution at which point the bot will place an order to restore the coin to the target distribution."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Order Type"}</b></div>
                                        <div>{"Market Orders get executed immediately, but the price is not guaranteed. Limit Orders have a preset price, but it may take time before the order is filled. If the order is not filled after the select time out period, the order is canceled. High-Margin uses the ask price when selling, and the bid price when buying. Low-Margin uses the bid price when selling, and the ask price when buying. High-Margin limit orders may take longer to get filled, but at a better price."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Cooldown Trigger"}</b></div>
                                        <div>{"At times of high price volatility, Balance Bot may perform many trades to keep your portfolio balanced. This may not be preferred, because of the resulting trading fees. To avoid such situation, you may choose to trigger a 1 hour cooldown period, which kicks in when the set number of trades are performed within a 5 minute period."}</div>
                                        <div>&nbsp;</div>
                                        <div><b>{"Automatic Mode"}</b></div>
                                        <div>{"You can operate your bot manually. In this mode, you will need to keep an eye on the dashboard and place manual orders when the dashboard indicates that a coin should be bought or sold. This is useful if you want to have full control, typically when you use Balance Bot for the first time and you want to get a feel for how it works."}</div>
                                        <div>&nbsp;</div>
                                        <div>{"Enabling automatic mode is a lot more practical though, as Balance Bot will run 24/7 and place orders automatically to keep your portfolio balanced."}</div>
                                        <div>&nbsp;</div>
                                        <div><i>{"Note: "}</i>{"You don't need to keep your browser open to run your bot in automatic mode. The application will keep running in the background. You can open the application in the browser at any time to check the status of your bot."}</div>
                                    </Box>
                                </React.Fragment>
                            }
                            onClose={handleTooltipClose}
                            open={open}
                            disableFocusListener
                            disableHoverListener
                            disableTouchListener
                        >
                            <IconButton onClick={open ? handleTooltipClose : handleTooltipOpen}>
                                <InfoOutlinedIcon color="secondary" />
                            </IconButton>
                        </HtmlTooltip>
                    </ClickAwayListener>
                </Grid>
            </Grid>
            <Grid container spacing={3}>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="botname"
                        name="botname"
                        label="Bot Name"
                        helperText="Give your bot a name"
                        value={botname}
                        onChange={onBotname}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="threshold"
                        name="threshold"
                        label="Balancing Threshold (%)"
                        helperText="At what deviation from the target distribution should be rebalanced"
                        value={threshold}
                        onChange={onThreshold}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={6}>
                    <FormControl>
                        <InputLabel>Order Type</InputLabel>
                        <Select
                            labelId="order-type"
                            name="ordertype"
                            value={ordertype}
                            onChange={onOrdertype}
                            className={classes.textinput}
                        >
                            <MenuItem value={'market'}>Market Order</MenuItem>
                            <MenuItem value={'limit-high'}>Limit Order &gt; High-Margin</MenuItem>
                            <MenuItem value={'limit-low'}>Limit Order &gt; Low-Margin</MenuItem>
                        </Select>
                        <FormHelperText>Select the order type to be used for trades</FormHelperText>
                    </FormControl>
                </Grid>
                <Grid item xs={6}>
                    <FormControl>
                        <InputLabel>Limit Order Time Out</InputLabel>
                        <Select
                            labelId="limit-time-out"
                            name="timeout"
                            value={timeout}
                            onChange={onTimeout}
                            className={classes.textinput}
                            disabled={ordertype === 'market'}
                        >
                            <MenuItem value={10}>10 minutes</MenuItem>
                            <MenuItem value={20}>20 minutes</MenuItem>
                            <MenuItem value={30}>30 minutes</MenuItem>
                            <MenuItem value={60}>60 minutes</MenuItem>
                            <MenuItem value={90}>90 minutes</MenuItem>
                            <MenuItem value={120}>120 minutes</MenuItem>
                        </Select>
                        <FormHelperText>Select the maximum wait time for limit orders to be filled</FormHelperText>
                    </FormControl>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="cooldown"
                        name="cooldown"
                        label="Cooldown Trigger"
                        helperText="After how many orders within 5 minutes should Balance Bot go into 'cooldown' mode (set to 0 to disable)"
                        value={cooldown}
                        onChange={onCooldown}
                        className={classes.textinput}
                    />
                </Grid>
                <Grid item xs={12}>
                    <FormControlLabel
                        control={<Checkbox color="secondary" name="headless" checked={headless} onChange={onHeadless} />}
                        label="Run bot in automatic mode"
                    />
                </Grid>
            </Grid>
        </React.Fragment>
    );
}